* {
  margin: 0px;
  padding: 0px;
  font-family: 'Roboto', sans-serif;
}

.App {
  height: 100%;
}

body {
  background: rgb(216, 212, 212);
  background-image: url("y-so-serious-white.png");
}

.logo {
  padding: 5px;
  margin: auto;
  display: flex;
  flex: auto;
  cursor: pointer;
  font-weight: bold;
}

.nav-element {
  padding: 5px 15px;
  display: flex;
  cursor: pointer;
  line-height: auto;
}

header {
  flex-direction: row;
  display: flex;
  text-align: center;
  background: #282c34;
  color: #fff;
  justify-content: flex-start;
  width: 100vw;
}

nav {
  display: flex;
  flex-direction: row;
}

input {
  font-size: 40px;
  display: flex;
  flex-direction: center;
  text-align: center;
  margin: 50px 0px 0px 0px;
  color: #282c34;
}

.Center {
  padding: 25px 0px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: flex;
}

.leftText {
  text-align: left;
}

.blackBack {
  background-color: #282c34
}



