.TopBar {
  background: white;
  text-align: center;
  width: 100%;
}

.LowerBar {
  text-align: center;
  font-size: 40px;
  color: white;
}

.IssueTop {
  background: #282c34;
  font-size: 30px;
  display: flex;
  color: white;
  margin-top: 30px;
  text-decoration: underline;
  padding: 10px 10px;
}

.IssueBottom {
  background: white;
  font-size: 30px;
  display: flex;
  border: 1px #282c34 solid;
  padding: 10px
}

.IssueList {
  display: flex;
  flex-direction: column;
}

.strtitle {
  color: #282c34;
  font-weight: bold;
  font-size: 45px;
}

.strButton{
  margin: 50px 0px 0px 0px;
  background: #282c34;
  color: white;
  outline: white 2px solid;
  font-weight: bold;
}

.rowflex {
  display: flex;
  flex-direction: row;
}